<script lang="ts">
	import { reactive, ref, watch } from "vue";
	import TableWidgetList, { TableDataPropModel } from "./TableListWidget.vue";
	import WgHeroConfig from "@/widgets/config/WgHeroConfig.vue";
	import WgMapConfig from "@/widgets/config/WgMapConfig.vue";
	import WgMapIrrigationConfigVue from '@/widgets/config/WgMapIrrigationConfig.vue';
	

	interface dataReactiveInterface {
		onEdit: boolean;
		searchText: string;
		classSearchWrapper: Array<string>;
		dataTableListWidget: TableDataPropModel;
		showSelector: false;
		selectedItem: number;
	}

	export interface widgetEvent {
		showSelector: boolean;
		onEdit: boolean;
	}

	export default {
		setup(props, ctx) {
			let dataReactive: dataReactiveInterface = reactive<dataReactiveInterface>({
				onEdit: false,
				classSearchWrapper: [],
				searchText: "",
				dataTableListWidget: {
					data: [
						{
							avatarUrl: "test url",
							description:
								"Hero merupakan spanduk untuk menampilkan informasi di halaman awal",
							ID: 0,
							title: "Hero",
							hidden: false,
						},
						{
							avatarUrl: "test url",
							description:
								"Menampilkan posisi device",
							ID: 1,
							title: "Peta Device",
							hidden: false,
						},
							{
							avatarUrl: "test url",
							description:
								"Membuat tampilan peta irigasi",
							ID: 2,
							title: "Peta Irigasi",
							hidden: false,
						},
					],
				},
				showSelector: false,
				selectedItem: -1,
			});
			const classFocus = "focus";
			const inputEl = ref<HTMLInputElement>();
			let onFocus: (e: Event) => void = (e: Event) => {
				if (!dataReactive.classSearchWrapper.includes(classFocus))
					dataReactive.classSearchWrapper.push(classFocus);
			};
			let onBlur: (e: Event) => void = (e: Event) => {
				if (dataReactive.classSearchWrapper.includes(classFocus))
					dataReactive.classSearchWrapper.splice(
						dataReactive.classSearchWrapper.indexOf(classFocus),
						1
					);
			};
			watch(dataReactive, (n: dataReactiveInterface) => {
				let e: widgetEvent = {
					showSelector: n.showSelector,
					onEdit: n.onEdit,
				};
				ctx.emit("change", e);
			});
			let onSubmit = (e: Event) => {
				e.preventDefault();
				e.stopPropagation();
			};
			let onCancelEdit = () => {
				dataReactive.onEdit = false;
				ctx.emit("cancelEdit");
			};
			let onSave = () => {
				ctx.emit("save");
			};

			let onItemSelected = (id: number) => {
				dataReactive.showSelector = false;
				dataReactive.selectedItem = id;
			};

			const components = [WgHeroConfig,WgMapConfig,WgMapIrrigationConfigVue];

			return {
				inputEl,
				dataReactive,
				onFocus,
				onBlur,
				onSubmit,
				onItemSelected,
				onCancelEdit,
				onSave,
				components,
			};
		},
		components: { TableWidgetList, WgHeroConfig,WgMapConfig },
		props: {
			layoutName: {
				type: String,
			},
		},
	};
</script>
