<script lang="ts">
	import { defineComponent, onMounted, ref } from "vue";
	import { moveEvtMobileFix } from "../mixin/irrigation/infrastructure/move-evt-mobile-fix.js";
	import { CanvasSmbl } from "../mixin/irrigation/infrastructure/canvas-smbl.js";
	import { moveScaleApplay } from "../mixin/irrigation/infrastructure/move-scale-applay.js";
	import { evtRouteApplay } from "../mixin/irrigation/infrastructure/evt-route-applay.js";
	import {
		copyPastApplay,
		groupSelectApplay,
	} from "../mixin/irrigation/diagram/group-select-applay.js";
	import { shapeTypeMap } from "../mixin/irrigation/shapes/shape-type-map.js";
	import "../mixin/irrigation/ui/menu.js";
	import "../mixin/irrigation/ui/shape-menu.js";
	import {tipShow} from "../mixin/irrigation/ui/ui";

	export default defineComponent({
		setup() {
			const containerDiagram = ref<HTMLElement>();
			onMounted(() => {
				const canvas = document.getElementById('canvas')! as any;
				const clientRect = containerDiagram.value?.getBoundingClientRect();
				canvas[CanvasSmbl] = {
					data: {
						position: { x: 0, y: 0 },
						offsetPosition: { x: clientRect?.x, y: clientRect?.y },
						scale: 1,
						cell: 24,
					},
					shapeMap: shapeTypeMap(canvas as any),
				};

				moveEvtMobileFix(canvas.ownerSVGElement);
				evtRouteApplay(canvas.ownerSVGElement);
				copyPastApplay(canvas);
				groupSelectApplay(canvas); // groupSelectApplay must go before moveScaleApplay
				moveScaleApplay(canvas);
				(document.getElementById('menu-shape') as any)!.init(canvas);
				tipShow(true)
			});

			return{
				containerDiagram
			}
		},
	});
</script>
