<template>
    <input type="file" id="chooseFile" name="media_file" @change="onChange" accept="image/*" capture="camera" class="cfg-mrt-file">
    <label for="chooseFile" class="cfg-mrt-file-label">
        <svg fill="currentColor" xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 -960 960 960" width="24"><path d="M440-320v-326L336-542l-56-58 200-200 200 200-56 58-104-104v326h-80ZM240-160q-33 0-56.5-23.5T160-240v-120h80v120h480v-120h80v120q0 33-23.5 56.5T720-160H240Z"/></svg>
    </label>
    <!-- <Teleport to="body">
        <div class="cfg-edit-img">
            <div class="result" ref="result"></div>
        </div>
    </Teleport> -->
</template>

<style>
.cfg-mrt-file{
    width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.cfg-mrt-file-label{
    position: absolute;
    right: 16px;
    cursor: pointer;
    color: #A3A6B7;
}
.cfg-edit-img{
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 21;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.425);
}
</style>


<script lang="ts">
import { ref } from 'vue'

export default {
    setup() {
        const result = ref<HTMLElement>();
        const onChange = (e:Event)=>{
            const file = (<HTMLInputElement>e.target!).files && (<HTMLInputElement>e.target!).files!.length ? (<HTMLInputElement>e.target!).files![0] : null
            if(file){
                const reader = new FileReader();
                reader.onload = e => {
                if (e.target!.result) {
                    // create new image
                    let img = document.createElement('img');
                    img.id = 'image';
                    img.src = e.target!.result as string;
                    // clean result before
                    result.value!.innerHTML = '';
                    // append new image
                    result.value!.appendChild(img);
                    // show save btn and options
                    // init cropper
                    // cropper = new Cropper(img);
                }
                };
                reader.readAsDataURL(file);
            }
        }

        return{
            onChange,
            result
        }
    },
}
</script>
