<template>
	<div class="gridster" ref="root">
		<div
			class="gridster-column"
			v-for="(item, idx) in dataReactive.gridsterComponent.gridColumns"
			:style="getGridColumnStyle(idx)"
			:key="idx"
		/>
		<div
			class="gridster-row"
			v-for="(item, idx) in dataReactive.gridsterComponent.gridRows"
			:style="getGridRowStyle(idx)"
			:key="idx"
		/>
		<slot
			v-if="dataReactive.ready"
			:gridster="dataReactive.gridsterComponent"
		/>
		<GridPreview
			:previewStyle="dataReactive.gridsterComponent.previewStyle$"
			:render="dataReactive.gridsterComponent.renderer"
			:renderer="dataReactive.gridsterComponent.gridRenderer"
		/>
	</div>
</template>

<style>
	@import "./gridster.css";
</style>

<script lang="ts">
	import { DisplayGrid, GridsterConfig } from "@/grid/gridsterConfig.interface";
	import { debounceTime, Subject, switchMap, takeUntil, timer } from "rxjs";
	import {
		onMounted,
		PropType,
		reactive,
		ref,
		onUnmounted,
		getCurrentInstance,
		watch,
	} from "vue";
	import { Renderer2Impl } from "./angular.impl";
	import { GridsterComponent } from "./gridster.component";
	import { GridsterComponentInterface } from "./gridster.interface";
	import GridPreview from "./grid-preview.vue";
import { GridsterItem } from './gridsterItem.interface';

	interface reactiveType {
		gridsterComponent: GridsterComponentInterface | null;
		ready: boolean;
	}

	export default {
		setup(props) {
			const root = ref();
			const dataReactive = reactive<reactiveType>({
				gridsterComponent: null,
				ready: false,
			});

			dataReactive.gridsterComponent = new GridsterComponent(
				new Renderer2Impl(),
				root.value
			);

			onMounted(() => {
				dataReactive.gridsterComponent!.el = root.value;
				dataReactive.gridsterComponent!.calculateLayout$ = new Subject<void>();
				dataReactive.gridsterComponent!.resize$ = new Subject<void>();
				dataReactive.gridsterComponent!.destroy$ = new Subject<void>();
				dataReactive.gridsterComponent!.options = Object.assign({}, props.options as GridsterConfig);
				dataReactive.gridsterComponent?.onChanges(
					dataReactive.gridsterComponent!.options
				);

				if (props.options?.initCallback) {
					props.options?.initCallback(
						dataReactive.gridsterComponent as GridsterComponentInterface
					);
				}
				dataReactive
					.gridsterComponent!.calculateLayout$.pipe(
						debounceTime(0),
						takeUntil(dataReactive.gridsterComponent!.destroy$)
					)
					.subscribe(() => dataReactive!.gridsterComponent!.calculateLayout());

				dataReactive
					.gridsterComponent!.resize$.pipe(
						switchMap(() => timer(100)),
						takeUntil(dataReactive.gridsterComponent!.destroy$)
					)
					.subscribe(() => dataReactive!.gridsterComponent!.resize());

				dataReactive.ready = true;
			});

			onUnmounted(() => {
				dataReactive.gridsterComponent!.destroy$.next();
				dataReactive.gridsterComponent!.previewStyle$.complete();
				if (dataReactive.gridsterComponent!.windowResize) {
					dataReactive.gridsterComponent!.windowResize();
				}
				if (
					dataReactive.gridsterComponent!.options &&
					dataReactive.gridsterComponent!.options.destroyCallback
				) {
					dataReactive.gridsterComponent!.options.destroyCallback(
						dataReactive.gridsterComponent as GridsterComponentInterface
					);
				}
				if (
					dataReactive.gridsterComponent!.options &&
					dataReactive.gridsterComponent!.options.api
				) {
					dataReactive.gridsterComponent!.options.api.resize = undefined;
					dataReactive.gridsterComponent!.options.api.optionsChanged = undefined;
					dataReactive.gridsterComponent!.options.api.getNextPossiblePosition =
						undefined;
					dataReactive.gridsterComponent!.options.api = undefined;
				}
				dataReactive.gridsterComponent!.emptyCell.destroy();
				dataReactive.gridsterComponent!.emptyCell = null!;
				dataReactive.gridsterComponent!.compact.destroy();
				dataReactive.gridsterComponent!.compact = null!;
				dataReactive.gridsterComponent!.calculateLayout$ = null!;
				dataReactive.gridsterComponent!.resize$ = null!;
				dataReactive.gridsterComponent!.destroy$ = null!;
				dataReactive.gridsterComponent!.options = null!;
			});

			watch(props.options!, ()=>{
				dataReactive.gridsterComponent!.options = Object.assign({}, props.options as GridsterConfig);
				dataReactive.gridsterComponent!.onChanges(dataReactive.gridsterComponent!.options)
			})

			const getGridColumnStyle = (idx: number) =>
				dataReactive.gridsterComponent!.gridRenderer.getGridColumnStyle(idx);
			const getGridRowStyle = (idx: number) =>
				dataReactive.gridsterComponent!.gridRenderer.getGridRowStyle(idx);

			return {
				dataReactive,
				root,

				getGridColumnStyle,
				getGridRowStyle,
			};
		},
		props: {
			options: {
				type: Object as PropType<GridsterConfig>,
				require: true,
			},
		},
		components: { GridPreview },
	};
</script>
