<template>
    <div class="gridster-preview" ref="root"></div>
</template>

<style>
.gridster-preview {
  position: absolute;
  display: none;
  background: rgba(0, 0, 0, 0.15);
}
</style>

<script lang="ts">
import { onMounted, onUnmounted, PropType, ref } from 'vue';
import {GridPreviewComponent} from "./gridPreview.component";
import { EventEmitter, Renderer2 } from './angular.interface';
import { GridsterItem } from './gridsterItem.interface';
import { GridsterRenderer } from './gridsterRenderer.service';

export default {
    setup(props){
        let component:GridPreviewComponent;
        const root = ref();
        
        onMounted(()=>{
            component = new GridPreviewComponent(props.previewStyle, props.renderer, props.render, root.value);
            component.init()
        })

        onUnmounted(()=>{
            component.destroy()
        })

        return{
            root
        }
    },
    props:{
        previewStyle:{
            type: Object as PropType<EventEmitter<GridsterItem | null>>,
            required: true
        },
        renderer: {
            type: Object as PropType<GridsterRenderer>,
            required: true
        },
        render:{
            type: Object as PropType<Renderer2>,
            required: true
        }
    }
}
</script>
