<template>
	<div class="gridster-item" ref="root" :style="{ zIndex: zIndexComputed }">
		<slot />
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.s && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-s"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.e && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-e"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.n && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-n"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.w && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-w"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.se && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-se"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.ne && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-ne"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.sw && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-sw"
		></div>
		<div
			@mousedown="comp.component.resize.dragStartDelay($event)"
			@touchstart="comp.component.resize.dragStartDelay($event)"
			v-if="comp.component.resize.resizableHandles?.nw && comp.component.resize.resizeEnabled"
			class="gridster-item-resizable-handler handle-nw"
		></div>
	</div>
</template>

<style>
	@import "./gridster-item.css";
	.gridster.mobile .gridster-item{
		position: relative;
	}
</style>


<script lang="ts">
	import {
		computed,
		onMounted,
		onUnmounted,
		PropType,
		reactive,
		ref,
		toRaw,
		watch,
	} from "vue";
	import { GridsterComponentInterface } from "./gridster.interface";
	import { GridsterItemComponent } from "./gridItem.component";
	import {
		GridsterItem,
		GridsterItemComponentInterface,
	} from "./gridsterItem.interface";
import { Subject, Subscription } from 'rxjs';

	interface reactiveType {
		component: GridsterItemComponentInterface;
		sub?: Subscription | null;
	}
	export default {
		props: {
			gridster: {
				type: Object as PropType<GridsterComponentInterface>,
				require: true,
			},
			item: {
				type: Object as PropType<GridsterItem>,
			},
			event: {
				type: Object as PropType<Subject<void>>
			}
		},
		setup(props) {
			const root = ref();
			const comp = reactive<reactiveType>({
				component: new GridsterItemComponent(
					props.gridster!,
					root.value,
					props.gridster!.zone
				),
				sub: null
			});
			
			comp.component.item = Object.assign({}, toRaw(props.item));
			comp.component.updateOptions();
			
			onMounted(() => {
				comp.component.el = root.value;
				props.gridster?.addItem(comp.component as GridsterItemComponentInterface);
				comp.sub = props.event?.subscribe(() =>{
					comp.component.item = Object.assign({}, toRaw(props.item));
					comp.component.updateOptions();
					comp.component.setSize();
				})
			});

			watch(comp.component.item, (n, p) => { 
				if (n) {
					comp.component.updateOptions();
					if (!comp.component.init) {
						props.item!.cols = comp.component.$item.cols
						props.item!.rows = comp.component.$item.rows
						props.item!.x = comp.component.$item.x
						props.item!.y = comp.component.$item.y
						comp.component.gridster.calculateLayout$.next();
					}
				}
				if (n && p) {
					comp.component.setSize();
				}
			});
			onUnmounted(() => {
				comp.sub?.unsubscribe();
				comp.sub = null;
				
				comp.component.gridster.removeItem(
					comp.component as GridsterItemComponentInterface
				);
				comp.component.drag.destroy();
				comp.component.resize.destroy();
				comp.component.gridster =
					comp.component.drag =
					comp.component.resize =
						null!;
			});
			const zIndexComputed = computed(
				() =>
					comp.component.getLayerIndex() +
					comp.component.gridster.$options.baseLayerIndex
			);

			return {
				root,
				zIndexComputed,
				comp
			};
		},
	};
</script>
